import React, { useState, useEffect } from "react";
import { firebase } from "./firebase";

function App() {
  const [tareas, setTareas] = useState([]);
  const [tarea, setTarea] = useState("");
  const [modoEdicion, setModoEdicion] = useState(false);
  const [id, setId] = useState(false);

  useEffect(() => {
    const obtenerDatos = async () => {
      try {
        //De esta manera llamamos a firestore al momento de que se renderize este componente.
        const db = firebase.firestore();
        //Los metodos y las distintas formas para trabajar con esta bbdd estan indicadas en la documentación. Dejo el enlace a continuación. https://firebase.google.com/docs/firestore/quickstart.
        // Dentro de los parámetros del metodo collection(nombre_collection) introduciremos el nombre de la colección que hayamos inicializado en la bbdd.
        const data = await db.collection("tareas").get();

        //almacenamos la informacion que nos devuelve la bbdd para guardarla en el state.
        const arrayData = data.docs.map((doc) => ({
          //extraemos el id
          id: doc.id,
          //extraemos la data.
          //hacemos una propagacion para que cree un duplicado del item que estamos manejando debido que de la otra forma es más complicado y javascript nos ayuda para obtener la data de manera más comoda.
          ...doc.data(),
        }));

        //almacenamos el array en el state
        setTareas(arrayData);
        console.log(arrayData);
      } catch (error) {
        console.log(error);
      }
    };
    obtenerDatos();
  }, []);

  const agregar = async (e) => {
    //evitamos que se recargue la página
    e.preventDefault();

    //validador de que el campo no este vacio.
    if (!tarea.trim()) {
      console.log("campo vacio");
      return;
    }

    //intentamos enviar los datos a la bbdd.
    try {
      //conexion con ddbb
      const db = firebase.firestore();
      //construimos un objeto para enviar a la ddbb con el mismo formato de datos a ser posible.
      const nuevaTarea = {
        name: tarea,
        date: Date.now(),
      };
      //usamos el metodo .add(objeto) para insertar los nuevos datos a la bbdd.
      const data = await db.collection("tareas").add(nuevaTarea);
      //Para refrescar la lista que tenemos creada tenemos que insertar manualmente el item que hemos creado a la lista actual. Al ser una funcion asincrona, pintamos directamente los datos y el id que nos devuelve firebase ya que, de haber algun error, este se saldria y saltaria el error.
      setTareas([
        //creamos una copia de los datos que ya existen en el state.
        ...tareas,
        //agregamos el objeto creado al state e incorporamos a dicho objeto el id que nos devuelve firebase al momento de agregar el nuevo elemento.
        { ...nuevaTarea, id: data.id },
      ]);

      //vaciamos el state para poder agregar nuevos datos a la bbdd.
      setTarea("");
    } catch (error) {
      console.log(error);
    }
  };

  //funcion para eliminar items
  const eliminar = async (id) => {
    try {
      //configuracion de la ddbb.
      const db = firebase.firestore();
      //indicamos el id que vamos a eliminar mediante el metodo .doc(id) y con la funcion delete() la eliminamos de la ddbb.
      await db.collection("tareas").doc(id).delete();

      const arrayFiltered = tareas.filter((item) => item.id !== id);
      setTareas(arrayFiltered);
    } catch (error) {
      console.log(error);
    }
  };

  //Funcion para cambiar a modo Edicion
  const activarEdicion = (item) => {
    setModoEdicion(true);
    setTarea(item.name);
    setId(item.id);
  };

  const editar = async (e) => {
    e.preventDefault();

    //validador de que el campo no este vacio.
    if (!tarea.trim()) {
      console.log("campo vacio");
      return;
    }

    try {
      //configuracion de firebase
      const db = firebase.firestore();
      //despues de haber capturado al id que se encuentra en el state, utilizamos el metodo update(obj) que se le envia como parametro el campo que deseamos actualizar. No hace falta indicar todos los campos, basta con enviar el campo que deseamos actualizar, el resto no serán afectados.
      //También, con este método podemos indicar un nuevo campo si estimamos conveniente.
      await db.collection("tareas").doc(id).update({
        name: tarea,
        //podriamos indicar un nuevo campo si deseamos con este método.
        /* pipi: "pipi" */
      });

      //actualizar lista local de items
      const arrayEditado = tareas.map((item) =>
        item.id === id ? { id: item.id, fecha: item.fecha, name: tarea } : item
      );
      setTareas(arrayEditado);
      setModoEdicion(false);
      setTarea("");
      setId("");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="container">
      <h1>Lista</h1>
      <div className="row">
        <div className="col-md-6">
          <h3>Lista de elementos</h3>
          <ul className="list-group">
            {tareas.map((item) => (
              <li key={item.id} className="list-group-item">
                {item.name}
                <button
                  className="btn btn-danger btn-sm float-end"
                  onClick={() => eliminar(item.id)}
                >
                  Eliminar
                </button>
                <button
                  className="btn btn-warning btn-sm float-end me-2"
                  onClick={() => activarEdicion(item)}
                >
                  Editar
                </button>
              </li>
            ))}
          </ul>
        </div>
        <div className="col-md-6">
          <h3>{modoEdicion ? "Editar Tarea" : "Agregar Tarea"}</h3>
          <form onSubmit={modoEdicion ? editar : agregar}>
            <input
              type="text"
              placeholder="Ingrese Tarea"
              className="form-control mb-2"
              onChange={(e) => setTarea(e.target.value)}
              value={tarea}
            />
            <button
              className={
                modoEdicion ? "btn btn-warning col-12" : "btn btn-dark col-12"
              }
              type="submit"
            >
              {modoEdicion ? "Editar Tarea" : "Agregar Tarea"}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default App;
